import React from 'react';
import './App.css';
import Navbar from './components/Navbar/Navbar';
import DialogsContainer from "./components/Dialogs/DialogsContainer";
import UsersContainer from "./components/Users/UsersContainer";
import ProfileContainer from "./components/Profile/ProfileContainer";
import HeaderContainer from "./components/Header/HeaderContainer";
import Login from "./components/Login/Login2";
import {connect} from "react-redux";
import {Route} from "react-router-dom";
import {initialize} from "./redux/app-reducer"
import Preloader from "./components/Preloader/Preloader";
import GalleryContainer from "./components/Gallery/galleryContainer";
import GalleryHistory from "./components/Gallery/GalleryHistory/GalleryHistory";
import ProfileEditContainer from "./components/Profile/ProfileEdit/ProfileEditContainer";


class App extends React.Component {

    componentDidMount () {
       this.props.initialize()
    }

    render () {
        if (!this.props.initialized) {
            return <Preloader/>
        }

        return (
          <>
            <HeaderContainer/>
            <div className="app-wrapper">
                <Navbar navbarState={this.props.state}/>
                <div className="app-wrapper-content">
                    <Route path='/dialogs' render={() => <DialogsContainer/>}/>
                    <Route path='/profile/:userId?' render={() => <ProfileContainer/>}/>
                    <Route path='/edit' render={() => <ProfileEditContainer/>}/>
                    <Route path='/users' render={() => <UsersContainer/>}/>
                    <Route path='/login' render={() => <Login/>}/>
                    <Route path='/news' render={() => <GalleryContainer/>}/>
                    <Route path='/history' render={() => <GalleryHistory/>}/>
                </div>
            </div>
           </>

        );
    }
}

let mapStateToProps = (state) => ({initialized: state.appPage.initialized})


export default connect (mapStateToProps, {initialize}) (App);


